html{
  background-color:black;
  background-repeat: no-repeat;
  
  height: 100%;
}
@keyframes backgroundFade {
  0%{
    background-color: #010552;
    background-repeat: no-repeat;
  }33%{
    background-color: #000113;
    background-repeat: no-repeat;
  }
  66%{
    background-color: #010552;
    background-repeat: no-repeat;
  }90%{
    background-color: #000785;
    background-repeat: no-repeat;
  }
}
.App {
  text-align: center;
}

.App-logo {
  margin: auto;
  width: 10vw;
  height: 12vh;
  pointer-events: none;
  display: row;
  grid: 1;
}

.cardText{
  color: rgb(238, 115, 0,1);
  grid-column: 2;
  text-align: center;
  text-shadow: 1px 1px  rgb(238, 0, 0);
  margin-top: 3.5vh;

  font-size: 1rem;
    
}

.container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 10px;
  margin: 25px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.soundCard{
  background: rgb(0,212,255);
background: linear-gradient(90deg, rgb(0, 162, 194) 0%, rgba(9,9,121,1) 45%, rgba(2,0,36,1) 84%);
  border-radius: 10px;
  border: 2px solid black;
  margin: auto;
  display: grid;
  row-gap: 5px;
  width: 22vw;
  height: 12vh;
  grid-template-columns: 1fr 2fr;
  grid-column: 1fr;
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
  margin-top: 120px;
  margin-bottom: 50px;
}
@media only screen and (max-width: 600px) {
  .container {
    grid-template-columns: 1fr;
  }
  .soundCard{
    width: 90vw;
    margin: auto;
  }
  .container{
    margin-top: 30%;
  }
  #top > input{
    width: 35%;
  }
  .cardText{
    font-size: 0.8rem;
  }
}
.soundCard:hover{
  transform:  scale(1.0125);
}

#top{
  background-color: rgb(0, 0, 88);
  text-align: center;
  position: fixed;
  top: 0;
  width: 100vw;
  padding: 8px;
  font-size: 12px;
  padding-top: 2px;
  z-index: 10;
}
#top > h1{
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  letter-spacing:  3px;
  color: rgb(238, 0, 0);
  text-shadow: 5px 5px 0px black;
}
#top > input{
  width: 20%;
  padding: 6px;
  border-radius: 25px;
  text-align: center;
}